<template>
    <el-container>
        <el-main>
            <el-form :model="saveForm" ref="saveForm">
                <el-table
                    :data="saveForm.tData"
                    class="severitytable"
                    style="width: 100%"
                    height="81vh"
                >
                    <el-table-column width="120" label="条件列表" prop="etname">
                    </el-table-column>
                    <el-table-column
                        prop="ettypename"
                        label="事件类型"
                        width="80"
                    >
                    </el-table-column>
                    <el-table-column
                        prop="etdatatype"
                        label="数据类型"
                        width="100"
                    >
                    </el-table-column>

                    <el-table-column prop="" label="1.轻微的" width="100">
                        <template slot-scope="scope">
                            <el-input
                                style="width: 30px"
                                size="mini"
                                v-if="scope.row.etsrtype == 1"
                                v-model="scope.row.from1"
                            ></el-input
                            ><span v-if="scope.row.etsrtype == 1">~</span
                            ><el-input
                                style="width: 30px"
                                size="mini"
                                v-if="scope.row.etsrtype == 1"
                                v-model="scope.row.to1"
                            ></el-input>

                            <el-radio
                                v-if="scope.row.etsrtype == 2"
                                v-model="scope.row.type2level"
                                :label="1"
                                >&nbsp;</el-radio
                            >
                        </template>
                    </el-table-column>
                    <el-table-column prop="" label="2.较小的" width="100">
                        <template slot-scope="scope">
                            <el-input
                                style="width: 30px"
                                size="mini"
                                v-if="scope.row.etsrtype == 1"
                                v-model="scope.row.from2"
                            ></el-input
                            ><span v-if="scope.row.etsrtype == 1">~</span>
                            <el-input
                                style="width: 30px"
                                size="mini"
                                v-if="scope.row.etsrtype == 1"
                                v-model="scope.row.to2"
                            ></el-input>
                            <el-radio
                                v-if="scope.row.etsrtype == 2"
                                v-model="scope.row.type2level"
                                :label="2"
                                >&nbsp;
                            </el-radio>
                        </template>
                    </el-table-column>
                    <el-table-column prop="" label="3.较大的" width="100">
                        <template slot-scope="scope">
                            <el-input
                                style="width: 30px"
                                size="mini"
                                v-if="scope.row.etsrtype == 1"
                                v-model="scope.row.from3"
                            ></el-input
                            ><span v-if="scope.row.etsrtype == 1">~</span>
                            <el-input
                                style="width: 30px"
                                size="mini"
                                v-if="scope.row.etsrtype == 1"
                                v-model="scope.row.to3"
                            ></el-input>
                            <el-radio
                                v-if="scope.row.etsrtype == 2"
                                v-model="scope.row.type2level"
                                :label="3"
                                >&nbsp;</el-radio
                            ></template
                        >
                    </el-table-column>
                    <el-table-column prop="" label="4.重大的" width="100">
                        <template slot-scope="scope">
                            <el-input
                                style="width: 30px"
                                size="mini"
                                v-if="scope.row.etsrtype == 1"
                                v-model="scope.row.from4"
                            ></el-input
                            ><span v-if="scope.row.etsrtype == 1">~</span>
                            <el-input
                                style="width: 30px"
                                size="mini"
                                v-if="scope.row.etsrtype == 1"
                                v-model="scope.row.to4"
                            ></el-input>
                            <el-radio
                                v-if="scope.row.etsrtype == 2"
                                v-model="scope.row.type2level"
                                :label="4"
                                >&nbsp;</el-radio
                            ></template
                        >
                    </el-table-column>
                    <el-table-column prop="" label="5.特大的" width="100">
                        <template slot-scope="scope">
                            <el-input
                                style="width: 30px"
                                size="mini"
                                v-if="scope.row.etsrtype == 1"
                                v-model="scope.row.from5"
                            ></el-input
                            ><span v-if="scope.row.etsrtype == 1">~</span>
                            <el-input
                                style="width: 30px"
                                size="mini"
                                v-if="scope.row.etsrtype == 1"
                                v-model="scope.row.to5"
                            ></el-input>
                            <el-radio
                                v-if="scope.row.etsrtype == 2"
                                v-model="scope.row.type2level"
                                :label="5"
                                >&nbsp;</el-radio
                            ></template
                        >
                    </el-table-column>
                    <el-table-column prop="" label="6.灾难的" width="100">
                        <template slot-scope="scope">
                            <el-input
                                size="mini"
                                style="width: 30px"
                                v-if="scope.row.etsrtype == 1"
                                v-model="scope.row.from6"
                            ></el-input
                            ><span v-if="scope.row.etsrtype == 1">~</span>
                            <el-input
                                style="width: 30px"
                                size="mini"
                                v-if="scope.row.etsrtype == 1"
                                v-model="scope.row.to6"
                            ></el-input>
                            <el-radio
                                v-if="scope.row.etsrtype == 2"
                                v-model="scope.row.type2level"
                                :label="6"
                                >&nbsp;</el-radio
                            ></template
                        >
                    </el-table-column>
                    <el-table-column label="操作" width="80" v-if="canSave">
                        <template slot-scope="scope">
                            <el-button
                                v-if="canSave"
                                plain
                                type="primary"
                                size="small"
                                @click="handleSave(scope.$index, scope.row)"
                                >保存</el-button
                            >
                        </template>
                    </el-table-column>
                </el-table>
            </el-form>
        </el-main>
    </el-container>
</template>
<script>
import { getseverity, setseverity } from "../../api/api";
import { getButtonList } from "../../promissionRouter";
export default {
    data() {
        return {
            canSave: false,
            saveForm: {
                tData: [],
            },
            levelName: {
                1: "轻微的",
                2: "较小的",
                3: "较大的",
                4: "重大的",
                5: "特大的",
                6: "灾难的",
            },
            saveLoading: false,
            options: [
                {
                    value: 1,
                    label: "低",
                },
                {
                    value: 2,
                    label: "中",
                },
                {
                    value: 3,
                    label: "高",
                },
                {
                    value: 4,
                    label: "超高",
                },
            ],
        };
    },
    methods: {
        getAllData() {
            getseverity().then((res) => {
                if (res.success) {
                    res.data.forEach((item) => {
                        Object.keys(item).map((i) => {
                            item[i] = item[i] == -1 ? "" : item[i];
                        });
                    });
                    this.saveForm.tData = res.data;
                }
            });
        },
        async handleSave(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });
                return;
            }

            //let arr = [];
            for (var i in row) {
                let from = i.indexOf("from");
                let to = i.indexOf("to");
                if (from != -1) {
                    let index = i.split("from")[1];
                    if (row["to" + index] == undefined) {
                        row["to" + index] = -1;
                    }
                }
                if (to != -1) {
                    let index = i.split("to")[1];
                    if (row["from" + index] == undefined) {
                        row["from" + index] = -1;
                    }
                }
            }
            let para = Object.assign({}, row);

            setseverity(para).then((res) => {
                if (res.success) {
                    this.$message({
                        message: "设置成功",
                        type: "success",
                    });

                    this.getAllData();
                } else {
                    this.$message({
                        message: res.msg,
                        type: "error",
                    });
                }
                this.saveLoading = false;
            });
        },
    },
    mounted() {
        this.getAllData();

        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            if (element.Func == "handleSave") {
                this.canSave = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>

<style scoped>
.severitytable /deep/ .el-input__inner {
    padding-left: 0px;
    padding-right: 0px;
}
</style>


